@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&family=Manrope:wght@100;200;300;400;500;600;700;800;900&display=swap);
body {
  font-size: 1rem;
  font-weight: 400px;
  height: 100%;
  width: 100%;
  margin: 0;
  color: #404040; /* or 4A4A4A (Tetra Pak Recommended) */
  font-family: "Montserrat", "Manrope", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Common Page Css */
.sidebar-header-layout {
  height: 100vh !important;
}

.content-wrapper {
  padding: 1.5rem 2rem 1.5rem;
  flex: 1 1;
}

.content-layout {
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
}

.ant-layout {
  background: #f6f6f6;
  position: relative;
}

.footer {
  text-align: center;
  background: #f6f6f6;
}

/* Modal */
.ant-modal-content {
  border-radius: 8px !important;
}

/* Popover */
.ant-popover-inner {
  border-radius: 8px !important;
}

/* Removing margins from each form */
.ant-form-item {
  margin-bottom: 0;
}

/* Lists */
.ant-list-lg .ant-list-item{
  padding:16px 12px;
}

/* Avatar (ICON) */
.ant-avatar > img {
  object-fit: contain;
}

/* Fonts */
.blue-text {
  color: #00bdf2;
}

.dark-blue-text {
  color: #023f88;
}

.sub-header {
  color: #777777;
  font-weight: 500;
}

h1 {
  font-weight: 700;
  font-size: 32px;
  margin-bottom: 0;
  color: black;
}

h2 {
  font-weight: 700;
  font-size: 16px;
  margin-bottom: 0;
  color: #404040;
}

h3 {
  font-weight: 500;
  font-size: 16px;
  margin-bottom: 0;
  color: #404040;
}

h4 {
  font-weight: 700;
  font-size: 18px;
  margin-bottom: 0;
  color: #404040;
}

@media (min-width: 600px) {
  h1 {
    font-size: 22px;
  }

  h2 {
    font-size: 18px;
  }
}

@media (min-width: 1000px) {
  h1 {
    font-size: 28px;
  }

  h2 {
    font-size: 18px;
  }

  h4 {
    font-size: 18px;
  }
}

